.hero {
	padding: 50px 0;
	&-block {
		display: flex; }
	&-text {
		flex-basis: 50%;
		padding-right: 140px;
		&__title {
			font-size: 41px;
			font-weight: 750;
			line-height: 55px;
			margin-bottom: 40px; }
		&__subtitle {
			display: inline-block;
			font-size: 20px;
			font-weight: 500;
			margin-bottom: 69px; } }
	&-list {
		margin: 0;
		padding: 0;
		&__item {
			display: flex;
			align-items: center;
			margin-bottom: 25px; }
		&__image {
			width: 40px;
			height: 40px;
			border: 2px solid $main-color;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 50%;
			margin-right: 30px; }
		&__text {
			max-width: 300px; } } }

@media (max-width: 1200px) {
	.hero {
		&-block {
			flex-direction: column; }
		&-list {
			margin-bottom: 44px; }
		&-text {
			padding-right: 10px; } } }

@media (max-width: 768px) {
		.hero {
			&-text {
				&__title {
					font-size: 24px;
					line-height: 1.4;
					margin-bottom: 15px; }
				&__subtitle {
					font-size: 16px; } }
			&-list {
				font-size: 12px; } } }
